import { handleActions } from 'redux-actions'
import { UIState } from '../../types'
import {
  SET_MAIN_LOADER,
  SET_REDIRECT_PATH,
  SET_SHOW_IOS_SWIPE_TOAST,
  setMainLoaderActionType, setRedirectPathActionType,
  setShowIosSwipeToastActionType,
} from '../actions/ui'
import { getQueryParam } from '../../helpers'

const initialState: UIState = {
  mainLoader: false,
  redirectPath: '',
  theme: getQueryParam('theme') || 'default',
  showIosSwipeToast: false,
}

const reducers = {
  [SET_MAIN_LOADER]: (
    state: UIState, { payload: mainLoader }: setMainLoaderActionType
  ): UIState => ({
    ...state, mainLoader
  }),
  [SET_REDIRECT_PATH]: (
    state: UIState, { payload: redirectPath }: setRedirectPathActionType
  ): UIState => ({
    ...state, redirectPath
  }),
  [SET_SHOW_IOS_SWIPE_TOAST]: (
    state: UIState, { payload: showIosSwipeToast }: setShowIosSwipeToastActionType
  ): UIState => ({
    ...state, showIosSwipeToast
  }),
}

export const ui = handleActions<UIState, any>(reducers, initialState)
